@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {  
    overflow: hidden;
    padding-top: 7rem;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}


.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.9rem;
    gap: 0.9rem;
    position: absolute;
    left: 0;
    bottom: 3.5rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


@media screen and (max-width: 1024px) {
.container{
    height: 100vh;
}

}

@media screen and (max-width: 600px) {
    .container {
        height: 100vh;
    }

    .header__socials,
    .scroll__down {
        display: none;
    }
}
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(16px);
            backdrop-filter: blur(16px);

}

nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);    
}

nav a.active {
   background: var(--color-bg);
   color: var(--color-white);
   
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 16%;
    gap: 16%;
}


.about__me {
    width: 100%;
    aspect-ratio: 1/1.3;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    align-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    text-align: center;
    transition: var(--transition)
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.about__card h5 {
     font-size: 0.9rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light)
}

.about__content p {
    color: var(--color-light);
    margin: 2rem 0 2.5rem;
}



@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0; 
    }

    .about__me {
        width: 50%;
        margin: 3rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;

    }
    }
    
    @media screen and (max-width: 600px) {    
      .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    } 

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.6rem 0;
    }
    
        
    }
.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary)
}

@media screen and (max-width: 1024px) {
      .skills {
        margin-top: 29rem;
      }
    
    .experience__container {
        grid-template-columns: 1fr;
      }

      .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
      }

      .experience__content {
        padding: 1rem;
      }
    }
    
    @media screen and (max-width: 600px) {
        .experience__container {
            grid-gap: 1rem;
            gap: 1rem;
        }
      
        .experience__container > div {
            width: 100%;
            padding: 2rem 1rem;
        }
    
        
    }
.services__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.service {
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.service:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service__head {
    background: var(--color-primary);
    padding: 1.2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 2rem rgba(0, 0, 0, 0.1);

}

.service__head h3{
    color: var(--color-bg);
    text-align: center;
    font-size: 1rem;
     
}

.service__list {
    padding: 2rem;
}

.service__list li {
    display: flex;
    grid-gap: 0.7rem;
    gap: 0.7rem;
    margin-bottom: 0.8rem;
}

.service__list-icon {
    color: var(--color-primary);
    margin-top: 2px;
}

.service__list p {
    font-size: 0.9rem;
}


@media screen and (max-width: 1024px) {
    .services__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }

}


@media screen and (max-width: 600px) {
    .services__container {
        grid-template-columns: 1fr;
        grid-gap: 1.6rem;
        gap: 1.6rem;
    }

}


.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background-color: var(--color-bg-variant);
    padding: 1.5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    background-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.3rem 0 2rem;
}

.portfolio__item-cta {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    margin-bottom: 1rem;
}


@media screen and (max-width: 1024px) {
    .workportfolio {
        margin-top: 10rem;
     
    }
    .portfolio__container{
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}

@media screen and (max-width: 600px) {

    .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
        
    }
    .workportfolio {
        margin-top: 17rem;
       
    }

}

.container.contact__container {
   width: 58%;
   display: grid;
   grid-template-columns: 30% 58%;
   grid-gap: 12%;
   gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem
}

.contact__option {
    background: var(--color-bg-variant);
    border-radius: 1.2rem;
    padding: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
  
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
}



form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.4rem;
    background: transparent;
    resize: none;
    border: 1px solid var(--color-primary-variant);
    color: var(--color-white);
}

@media screen and (max-width: 400px){

     .container.contact__container {
        width: var(--container-width-sm);
        grid-template-columns: 1fr;
    }

    #contact {
        margin-top: 135rem;
    }
}


@media screen and (min-width: 401px) and (max-width: 600px) {

    .container.contact__container {
        width: var(--container-width-sm);
        grid-template-columns: 1fr;
    }

    #contact {
        margin-top: 200rem;
    }
}



@media screen and (min-width: 601px) and (max-width: 1024px) {

    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }

    #contact {
        margin-top: 107rem;
    }
}


*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: 0;
    list-style: none;
    text-decoration: none;
}

:root {
--color-bg: #1f1f38;
--color-bg-variant: #2c2c6c;
--color-primary: #4db5ff;
--color-primary-variant: rgba(34, 68, 255, 0.4);
--color-white: #fff;
--color-light: rgba(253, 253, 251, 0.6);

--transition: all 400ms ease;

--container-width-lg: 75%;
--container-width-md: 86%;
--container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::webkit-scrollbar {
    display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #1f1f38;
  background-color: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
}


.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}


section > h2, section > h5 {
    text-align: center;
    color: rgba(253, 253, 251, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(253, 253, 251, 0.6);
    color: var(--color-light)
}

a {
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.55rem 1rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}

img {
    width: 100%;
    display: block;
    object-fit: cover;
}





@media screen and (max-width: 1024px) {
    .container {
         width: 86%;
         width: var(--container-width-md);
    }

    section {
        margin-top: 8rem;   
    }
}


@media screen and (max-width: 600px) {
    .container {
         width: 90%;
         width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;   
    }

}




